import React from 'react';
import Medal from './Medal';
import Paper from '@material-ui/core/Paper';
import { Divider } from '@material-ui/core';
import Badge from '@material-ui/core/Badge';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  country: {
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: 10,
    padding: 18,
    width: 260,
  },
  divider: {
    marginTop: 5,
    marginBottom: 10,
  },
  countryName: {
    fontSize: '1.8em',
    paddingRight: '8px',
  },
});

const Country =(props) => {
  const { country, medals, onIncrement, onDecrement, onDelete } = props;
  const classes = useStyles();

  const getMedalsTotal = (country, medals) => {
    let sum = 0;
    medals.forEach(medal => { sum += country[medal.name]; });
    return sum;
  }

  return (
    <Paper elevation={4} className={classes.country}>
      <Grid container spacing={0}>
        <Grid item xs={10}>
          <Badge badgeContent={ getMedalsTotal(country, medals) } color='primary'>
            <div className={classes.countryName}>
              { country.name }
            </div>
          </Badge>
        </Grid>
        <Grid item xs={2}>
          <IconButton aria-label="delete" onClick={() => onDelete(country.id)} style={{ padding: 6 }}>
            <DeleteIcon fontSize="medium" />
          </IconButton>
        </Grid>
      </Grid>
      <Divider className={classes.divider} />
      { medals.map(medal =>
        <Medal 
          key={ medal.id } 
          country={ country } 
          medal={ medal } 
          onIncrement={ onIncrement } 
          onDecrement={ onDecrement } />
      ) }
    </Paper>
  );
}

export default Country;
